* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #c4ffd1;
  background: linear-gradient(315deg, #65005e 3%, #3c84ce 38%, #30eee2 68%, #ff1919 98%) 0 0 / 400% 400% fixed;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  animation: gradient 35s infinite;
  overflow: auto;
}

h1, h2, li, p {
  font-family: Ubuntu Mono, monospace;
}

li {
  list-style-type: none;
}

p {
  margin: auto 10px;
}

#search-box {
  font: inherit;
  text-align: center;
  text-transform: uppercase;
  max-width: 320px;
  color: #15ff00;
  background-color: rgba(26, 3, 44, .267);
  border: rgba(0, 0, 0, 0);
  border-radius: 10px;
  margin-bottom: 60px;
  padding: 5px;
  font-family: Ubuntu Mono, monospace;
  font-size: 20px;
  font-weight: 700;
  display: block;
  box-shadow: 6px 7px 15px 1px rgba(49, 7, 58, .3);
}

#search-box:focus {
  outline: 1px solid rgba(36, 1, 39, .294);
}

.form__group {
  position: relative;
}

.title {
  text-align: center;
}

.input-country {
  margin-left: auto;
  margin-right: auto;
}

.country-symbols__img {
  border-radius: 10px;
  display: block;
}

.country-item__img {
  border-radius: 6px;
}

.country-symbols {
  justify-content: center;
  align-items: center;
  display: flex;
}

.country-name {
  color: #15ff00;
  margin-left: 10px;
  font-size: 30px;
}

.country-list-div {
  padding: 0;
}

.country-list-div__item {
  text-align: center;
  margin-bottom: 8px;
  font-size: 25px;
}

.li-bottom {
  padding-bottom: 25px;
}

.bold {
  color: #9eca00;
  margin-right: 4px;
  font-weight: bold;
}

.country-item {
  align-items: center;
  display: flex;
}

.country-list {
  margin: 0 auto;
  padding-left: auto;
  padding-right: auto;
}

.country-info {
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  width: 600px;
  background-color: rgba(6, 0, 12, .247);
  border-radius: 35px;
  margin: 0 auto;
  padding: 0;
  box-shadow: 6px 7px 15px 1px rgba(42, 4, 51, .192);
}

.name-country {
  font-size: 22px;
  font-weight: 700;
}

.country-item:not(:last-child) {
  margin-bottom: 15px;
}

@keyframes gradient {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 100% 100%;
  }

  100% {
    background-position: 0 0;
  }
}

.wave {
  width: 200%;
  height: 12em;
  opacity: .8;
  z-index: -1;
  background: rgba(255, 255, 255, .25);
  border-radius: 1000% 1000% 0 0;
  animation: wave 10s linear -3s infinite;
  position: fixed;
  bottom: 0;
  left: 0;
  transform: translate3d(0, 0, 0);
}

.wave:nth-of-type(2) {
  opacity: .8;
  animation: wave 18s linear infinite reverse;
  bottom: -1.25em;
}

.wave:nth-of-type(3) {
  opacity: .9;
  animation: wave 20s -1s infinite reverse;
  bottom: -2.5em;
}

@keyframes wave {
  2% {
    transform: translateX(1px);
  }

  25% {
    transform: translateX(-25%);
  }

  50% {
    transform: translateX(-50%);
  }

  75% {
    transform: translateX(-25%);
  }

  100% {
    transform: translateX(1px);
  }
}

